import React, { useState, useEffect, useRef } from "react";
import "./styles.css";
import * as turf from "@turf/turf";
import ReactMapGL, { Source, Layer, ScaleControl, Marker } from "react-map-gl";

// change to see effect

let lon = 151.205687597394,
  lat = -33.8655448786075;
var walkSpeedKph = 5;
var timeDistanceArray = [1, 3, 5, 15, 30];
var radius = walkSpeedKph / (60 / timeDistanceArray[3]);
var center = [lon, lat];
var options = { steps: 50, units: "kilometers", properties: { foo: "bar" } };
var circle = turf.circle(center, radius, options);

var line = turf.lineString(...circle.geometry.coordinates);
console.log(line);
export default function App() {
  const [viewport, setViewport] = useState({
    width: 1080,
    height: 960, // , [-87.61694, 41.86625]
    latitude: lat,
    longitude: lon,
    zoom: 14.5,
    pitch: 0,
    bearing: 0
  });
  const mapRef = useRef(null);

  return (
    <div className="App">
      {/* <a href="https://bdevg.com/articles/Source-and-Layer-component-of-react-map-gl/5fd769aae3b00a0008a72d01">
        <h1>Source and Layer component of react-map-gl</h1>
      </a> */}
      <ReactMapGL
        {...viewport}
        ref={mapRef}
        onClick={(e, v) =>
          console.log(
            e.features.forEach((feature) => {
              console.log(feature.layer.id.split("-")[1], e.lngLat);
            })
          )
        }
        transitionDuration={300}
        mapStyle="mapbox://styles/leehoss/ckpxosjd309zp18oi44abaxdq"
        mapboxApiAccessToken="pk.eyJ1IjoibGVlaG9zcyIsImEiOiJja3B4b2s2dXYwMDBhMndxc3R6YzYzZTAzIn0.qPzHaFsyFOX--FEKc2HJOQ"
        onViewportChange={(nextViewport) => setViewport(nextViewport)}
      >
        <div style={{ position: "absolute", bottom: 200, left: 100 }}>
          <ScaleControl maxWidth={100} unit={"metric"} />
        </div>
        <Marker
          latitude={-33.86554}
          longitude={151.20568}
          offsetLeft={-20}
          offsetTop={-10}
        >
          <div>You are here</div>
        </Marker>
        <Source id="my-data" type="geojson" data={circle}>
          <Layer
            id="point-90-hi"
            type="fill"
            paint={{
              "fill-color": "#088",
              "fill-opacity": 0.0,
              "fill-outline-color": "yellow"
            }}
          />
        </Source>

        <Source id="my-ata" type="geojson" data={line}>
          <Layer
            id="point-9-hi"
            type="line"
            paint={{
              "line-color": "#EDBA18",
              "line-opacity": 0.8,
              "line-width": 3
            }}
          />
        </Source>
      </ReactMapGL>
    </div>
  );
}
